___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
  font-family: "VCR";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

html {
  width: 100vw;
  height: 100vh;
}

p, h1, h2, h3, h4, h5, a, button {
  font-family: "VCR";
}

body {
  overflow: hidden;
  width: 100%;
  height: 100%;
  font-family: "VCR";
  /* background-image: url('./assets/dbd-bg.jpg'); */
  background-color: black;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0;
  position: relative;
}

body:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
  top: 0;
  left: 0;
}

.bg-img {
  position: absolute;
  height: 100vh;
  width: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

body h1 {
  margin-top: 0;
  color: #fff;
  font-size: 60px;
  text-align: center;
}

.voting-app {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

body button {
  font-size: 32px;
  padding: 8px 32px;
  border-radius: 0;
  border-style: dotted;
  background: #D61727;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 20px;
}

body button {
  outline-color: #fff;
}

body input:focus-visible,
body select:focus-visible {
  outline-color: #D61727;
}

body button:hover {
  text-decoration: underline;
  filter: brightness(1.2);
}

input {
  font-size: 20px;
  font-family: 'VCR';
  padding: 10px 20px;
}

.voting-app button#back {
  position: absolute;
  top: 10px;
  left: 10px;
}

.voting-app button#listvotes {
  position: absolute;
  bottom: 10px;
  left: 30px;
}

.voting-app button#announce {
  position: absolute;
  bottom: 10px;
  right: 30px;
}

/** -------- reconnect view ----------- **/
.voting-app[data-state="reconnect"] img {
  max-width: 95%;
}

.voting-app[data-state="reconnect"] input {
  margin: 30px auto 10px;
  min-width: 360px;
}

/** ---------- edit view --------- **/
.voting-app[data-state="edit"] p {
  color: #fff;
}

.voting-app select {
  font-family: 'VCR';
  font-size: 20px;
  padding: 10px 20px;
}

.voting-app[data-state="edit"] .alert-message,
.voting-app[data-state="edit"] .add-complete,
.voting-app[data-state="setup"] .setup-alert {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s ease;
  text-align: center;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 20px;
  width: 100%;
}

.voting-app[data-state="edit"] .alert-message.hidden,
.voting-app[data-state="edit"] .add-complete.hidden,
.voting-app[data-state="setup"] .setup-alert.hidden {
  opacity: 0;
  visibility: hidden;
}

.voting-app[data-state="edit"] .removed {
  display: none;
  pointer-events: none;
}

.voting-app[data-state="edit"] .edit-section {
  width: 80%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.voting-app[data-state="edit"] .nickname-inputs {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-wrap: wrap;
  margin: 0 auto 20px;
}

.voting-app[data-state="edit"] .nickname-inputs input {
  margin: 5px 10px 5px 0;
}

.voting-app[data-state="edit"] .nickname-inputs button#add-nickname {
  margin: 0;
}

.voting-app[data-state="edit"] .add-warning {
  text-align: center;
}

.voting-app[data-state="edit"] button#submit-changes,
.voting-app[data-state="edit"] button#submit-new,
.voting-app[data-state="edit"] input.new-killer {
  align-self: center;
}

.voting-app[data-state="edit"] button#add-new {
  position: absolute;
  top: 10px;
  right: 10px;
}

.voting-app[data-state="edit"] input.new-killer {
  margin-bottom: 10px;
}

.voting-app[data-state="edit"] .strike-btn {
  position: absolute;
  bottom: 10px;
  left: 10px;
  margin: 0;
}

/** ---------- setup view ---------- **/
.voting-app[data-state="setup"] .setup-alert {
  color: #fff;
  bottom: -50px;
}

.voting-app[data-state="setup"] input {
  margin-bottom: 10px;
  min-width: 360px;
}

.voting-app[data-state="setup"] .setup-section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/** ---------- announce view ---------- **/
.voting-app[data-state="announce"] .winner-select {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.voting-app[data-state="announce"] .winning-viewer {
  color: #fff;
  position: absolute;
  text-align: center;
  width: 100%;
  left: 0;
  bottom: 140px;
  font-size: 40px;
}`, "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;EAClB,4CAA2C;AAC7C;;AAEA;EACE,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kDAAkD;EAClD,uBAAuB;EACvB,sBAAsB;EACtB,4BAA4B;EAC5B,SAAS;EACT,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,8BAA8B;EAC9B,UAAU;EACV,MAAM;EACN,OAAO;AACT;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,WAAW;EACX,SAAS;EACT,QAAQ;EACR,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,WAAW;EACX,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,sBAAsB;AACxB;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,gBAAgB;EAChB,oBAAoB;EACpB,mBAAmB;EACnB,eAAe;EACf,yBAAyB;EACzB,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;;EAEE,sBAAsB;AACxB;;AAEA;EACE,0BAA0B;EAC1B,uBAAuB;AACzB;;AAEA;EACE,eAAe;EACf,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;AACb;;AAEA,0CAA0C;AAC1C;EACE,cAAc;AAChB;;AAEA;EACE,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA,qCAAqC;AACrC;EACE,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,kBAAkB;AACpB;;AAEA;;;EAGE,UAAU;EACV,mBAAmB;EACnB,yBAAyB;EACzB,kBAAkB;EAClB,kBAAkB;EAClB,aAAa;EACb,SAAS;EACT,6BAA6B;EAC7B,eAAe;EACf,WAAW;AACb;;AAEA;;;EAGE,UAAU;EACV,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,UAAU;EACV,aAAa;EACb,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,6BAA6B;EAC7B,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,kBAAkB;AACpB;;AAEA;;;EAGE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,WAAW;AACb;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,UAAU;EACV,SAAS;AACX;;AAEA,uCAAuC;AACvC;EACE,WAAW;EACX,aAAa;AACf;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA,0CAA0C;AAC1C;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,kBAAkB;EAClB,WAAW;EACX,OAAO;EACP,aAA